body {
  background-color: #f5f5f5;
  font-family: Roboto, sans-serif;
  font-weight: 400;
  line-height: 150%;
  font-size: 14px;
}
h1,
h2,
h3,
h4,
h5 {
  font-weight: 300;
}
.btn-lime {
  background-color: #14d700;
  color: white;
  border-radius: 0;
  text-transform: uppercase;
  -webkit-box-shadow: 0 1px 4px 0 rgba(0, 0, 0, 0.16), 0 1px 9px 0 rgba(0, 0, 0, 0.12);
  box-shadow: 0 1px 4px 0 rgba(0, 0, 0, 0.16), 0 1px 9px 0 rgba(0, 0, 0, 0.12);
}
.btn-lime:hover,
.btn-lime:active,
.btn-lime:focus {
  color: white;
  -webkit-box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.16), 0 2px 10px 0 rgba(0, 0, 0, 0.12);
  box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.16), 0 2px 10px 0 rgba(0, 0, 0, 0.12);
}
.navbar-default {
  border: 0;
  -webkit-box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.16), 0 2px 10px 0 rgba(0, 0, 0, 0.12);
  box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.16), 0 2px 10px 0 rgba(0, 0, 0, 0.12);
}
.navbar-default .navbar-brand {
  padding: 7px 7px;
}
.navbar-default .navbar-nav > li > a {
  border-bottom: 3px solid transparent;
}
.navbar-default .navbar-nav > li > a:hover {
  background-color: transparent;
  color: black;
  border-bottom: 3px solid transparent;
}
.navbar-default .navbar-nav > .open > a,
.navbar-default .navbar-nav > .active > a,
.navbar-default .navbar-nav > .active > a:hover {
  background-color: transparent;
  box-shadow: none;
  background-image: none;
  border-bottom: 3px solid #14d700;
  color: black;
}
article {
  background-color: white;
  position: relative;
  float: left;
  width: 100%;
  margin-bottom: 20px;
  -webkit-box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.16), 0 2px 10px 0 rgba(0, 0, 0, 0.12);
  box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.16), 0 2px 10px 0 rgba(0, 0, 0, 0.12);
}
article header {
  background-size: cover;
  position: relative;
  float: left;
  width: 100%;
}
article header h1 {
  font-size: 2.8rem;
  margin: 0;
}
article header h4 {
  margin: 10px 0 0 0;
  font-weight: 400;
  font-size: 1.5rem;
}
article header h4 a {
  color: #5cb4ff;
}
article header h4 a:hover {
  color: #5cb4ff;
  text-decoration: underline;
}
article header .heading-bg {
  background-color: rgba(0, 0, 0, 0.72);
  position: absolute;
  bottom: 10%;
  left: -10px;
  rigt: -10px;
  width: calc(100% + 20px);
  padding: calc(1rem + 10px);
  font-size: 1.8rem;
  color: #fff;
  font-weight: 300;
}
article header .heading-bg:before {
  content: ' ';
  position: absolute;
  width: 0;
  height: 0;
  left: 0px;
  top: 100%;
  border-width: 5px 5px;
  border-style: solid;
  border-color: rgba(0, 0, 0, 0.82) rgba(0, 0, 0, 0.82) transparent transparent;
}
article header .heading-bg:after {
  content: ' ';
  position: absolute;
  width: 0;
  height: 0;
  right: 0px;
  top: 100%;
  border-width: 5px 5px;
  border-style: solid;
  border-color: rgba(0, 0, 0, 0.82) transparent transparent rgba(0, 0, 0, 0.82);
}
article .content {
  padding: 1rem 1rem 0 1rem;
  float: left;
  width: 100%;
}
article .content h2 {
  font-size: 2.2rem;
}
article .content ul {
  list-style: none;
  padding: 0 0 1rem 0;
  margin: 0;
  color: #777;
}
article .content ul li {
  margin: 0.25rem 0;
  padding-left: 1rem;
  border-left: 3px solid #14d700;
  color: black;
}
article .content:after {
  content: " ";
  width: 100%;
  float: left;
  margin: 1rem 0 0 0;
  border-top: 1px solid silver;
}
article .content.nofter:after {
  content: none;
}
article footer {
  float: left;
  width: 100%;
  padding: 1rem;
}
.box {
  background-color: white;
  position: relative;
  float: left;
  width: 100%;
  margin-bottom: 20px;
  padding: 1rem;
  -webkit-box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.16), 0 2px 10px 0 rgba(0, 0, 0, 0.12);
  box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.16), 0 2px 10px 0 rgba(0, 0, 0, 0.12);
}
.box h3 {
  margin: 0 0 1rem 0;
  padding: 0;
}
.box ul {
  list-style: none;
  padding: 0 0 0 0;
  margin: 0;
  color: #777;
}
.box ul li {
  margin: 13px 0;
  padding-left: 1rem;
  border-left: 3px solid #777;
}
.box ul li a {
  display: block;
  height: 100%;
  color: #777;
  transition: all 0.2s ease-in-out;
}
.box ul li a:hover {
  text-decoration: none;
  color: black;
}
.box ul li:hover {
  border-left: 3px solid #14d700;
  transition: all 0.2s ease-in-out;
}
#comments nav {
  float: left;
  margin: 2rem 0;
  width: 100%;
}
#comments nav ul {
  list-style: none;
  border: none;
  padding: 0;
  margin: 0;
  width: 100%;
}
#comments nav ul li {
  border: none;
  padding: 0;
  margin: 0;
  floaT: left;
  display: block;
}
#comments nav ul li a {
  background-color: #14d700;
  -webkit-box-shadow: 0 1px 4px 0 rgba(0, 0, 0, 0.16), 0 1px 9px 0 rgba(0, 0, 0, 0.12);
  box-shadow: 0 1px 4px 0 rgba(0, 0, 0, 0.16), 0 1px 9px 0 rgba(0, 0, 0, 0.12);
  border: 0;
  border-radius: 0;
  color: white;
  text-transform: uppercase;
}
#comments nav ul li a:hover {
  -webkit-box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.16), 0 2px 10px 0 rgba(0, 0, 0, 0.12);
  box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.16), 0 2px 10px 0 rgba(0, 0, 0, 0.12);
}
#comments nav ul li.previous {
  float: left;
}
#comments nav ul li.next {
  float: right;
}
#comments .comment-list {
  list-style: none;
  margin: 0;
  padding: 0;
  float: left;
}
#comments .comment-list li {
  float: left;
  display: block;
  width: 100%;
}
#comments .comment-list li .box {
  box-shadow: none;
  padding: 0 0 1rem 0;
  border-left: 3px solid #14d700;
  padding-left: 1rem;
}
#comments .comment-list li .box .comment-text {
  padding-top: 1rem;
  padding-right: 1rem;
}
#comments .comment-list li .box .comment-author-meta {
  padding-left: 1rem;
}
#comments .comment-list li .box .avatar,
#comments .comment-list li .box .comment-data {
  padding: 0;
  padding-left: 0.8rem;
  float: left;
}
#comments .comment-list li .children {
  list-style: none;
  padding-left: 2rem;
  float: left;
}
#comments #respond {
  float: left;
}
#comments #respond #commentform p {
  margin: 0 0 10px;
  display: block;
  float: left;
  width: 100%;
}
#comments #respond #commentform p label {
  display: block;
}
#comments #respond #commentform p input,
#comments #respond #commentform p textarea {
  display: block;
  width: 100%;
}
.navigation.posts-navigation {
  float: left;
  width: 100%;
  margin-bottom: 20px;
}
.navigation.posts-navigation h2 {
  display: none;
}
.navigation.posts-navigation .nav-previous a,
.navigation.posts-navigation .nav-next a {
  padding: 6px 12px;
  background-color: #14d700;
  -webkit-box-shadow: 0 1px 4px 0 rgba(0, 0, 0, 0.16), 0 1px 9px 0 rgba(0, 0, 0, 0.12);
  box-shadow: 0 1px 4px 0 rgba(0, 0, 0, 0.16), 0 1px 9px 0 rgba(0, 0, 0, 0.12);
  border: 0;
  border-radius: 0;
  color: white;
  text-transform: uppercase;
}
.navigation.posts-navigation .nav-previous a:hover,
.navigation.posts-navigation .nav-next a:hover {
  -webkit-box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.16), 0 2px 10px 0 rgba(0, 0, 0, 0.12);
  box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.16), 0 2px 10px 0 rgba(0, 0, 0, 0.12);
  text-decoration: none;
}
div#gallery-2 dl.gallery-item {
  float: left;
  text-align: center;
  width: 23%;
  height: 100%;
  margin: 1%;
